@import '../variables.scss';

.get-in-touch {

  @include for-size(tablet) {
    align-items: center;
  }

  &__title {
    margin: 0 0 14px 0;
    text-align: center;
    font-weight: 500;
    font-size: 22px;
    line-height: 34px;


    @include for-size(tablet) {
      font-size: 18px;
      line-height: 34px;
      margin-bottom: 10px;
    }
  }

  &__tel {
    display: flex;
    font-size: 20px;
    line-height: 32px;
    text-decoration: none;
    color: $contact-tel-color;


    @include for-size(tablet) {
      font-size: 18px;
      text-decoration: underline;
      color: $accent-primary-color;
      text-decoration-color: $accent-primary-color;
    }

    &:hover {
      color: $accent-primary-color;
    }
  }

  &__icon {
    margin-right: 17px;
    align-items: center;

    @include for-size(tablet) {
      display: none;
    }

  }
}
