@import '../variables.scss';

.travel-list {
  margin: 0;
  padding: 0;

  @include for-size(phone-only) {
    width: 50%;
  }

  &__item {
    margin: 10px 0;
    padding: 0;
    font-size: 14px;
    line-height: 17px;
    list-style-type: none;
    color: $travel-list-item-color;
    cursor: pointer;
  }
}
