  @include for-size(tablet) {
    background: none;
  }

  &__body {
    overflow: hidden;
    max-width: 1170px;
    margin: 0 auto;

    @include for-size(tablet) {
      position: relative;
      max-width: 100%;
    }
  }

  &__nav {
    display: inline-flex;
    gap: 100px;
    border-bottom: 1px solid $info-nav-border;
    overflow: auto;
    max-width: 100%;

    @include for-size(tablet) {
      gap: 26px;
    }

    @include for-size(phone-only) {
      gap: 72px;
      margin-left: 15px;
    }
  }

  &__nav-button {
    margin: 0;
    padding: 15px 0;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: -0.415px;
    color: $info-nav-button-color;
    font-family: "Montserrat", "Arial", sans-serif;
    background: none;
    border: none;
    outline: none;
    cursor: pointer;

    &:hover {
      color: $accent-primary-color;
    }

    &--active {
      cursor: default;
      border: 0;
      pointer-events: none;
      color: $accent-primary-color;
    }

    @include for-size(tablet) {
      padding: 15px 37px;
    }

    @include for-size(phone-only) {
      padding: 15px;
    }

    &:first-child {
      @include for-size(phone-only) {
        margin-left: -15px;
      }
    }
  }

  &__list {
    position: relative;
    transition: all 0.3s ease-in-out;
    margin-bottom: 234px;

    @include for-size(tablet) {
      margin-bottom: 80px;
    }

    @include for-size(phone-only) {
      margin-bottom: 95px;
    }

    &--inline {
      display: flex;
      flex-wrap: nowrap;
    }
  }

  &__item {
    flex-shrink: 0;
    display: flex;
    flex-direction: row;
    align-items: start;
    width: 100%;
    margin-top: 80px;

    @include for-size(tablet) {
      flex-direction: column;
      align-items: center;
      margin-top: 0;
    }

    @include for-size(phone-only) {
      position: relative;
    }
  }

  &__general {
    max-width: 370px;
    margin: 0 100px 0 0;

    @include for-size(tablet) {
      max-width: 100%;
      display: flex;
      align-items: center;
      flex-direction: column;
      margin: 0 10px;
      padding: 0;
    }
  }

  &__title {
    margin: 15px 0 6px;
    font-weight: 600;
    font-size: 55px;
    line-height: 79px;
    letter-spacing: -1.245px;

    @include for-size(tablet) {
      margin: 32px 0 0;
      font-size: 30px;
      line-height: 45px;
      letter-spacing: 0;
    }

    @include for-size(phone-only) {
      margin: 24px 0 0;
      font-size: 24px;
      line-height: 36px;
    }
  }

  &__decorative-line {
    display: inline-block;
    align-self: self-end;
    height: 2.92px;
    width: 175px;
    background-color: $accent-primary-color;

    @include for-size(tablet) {
      display: none;
    }
  }

  &__text {
    margin: 23px 0 50px;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: -0.466667px;
    color: $info-text-color;

    @include for-size(tablet) {
      margin: 20px 0;
      text-align: center;
    }

    @include for-size(phone-only) {
      margin: 20px 10px;
    }
  }

  &__button {

    @include for-size(tablet) {
      padding: 19px 51px;
      margin-bottom: 30px;
      font-weight: 500;
      font-size: 10px;
      line-height: 12px;
    }

    @include for-size(phone-only) {
      display: block;
      width: calc(100% - 10px);
      margin-bottom: 56px;
    }
  }

  &__picture {
    padding: 0;
    @include for-size(tablet) {
      position: absolute;
      display: block;
      top: 366px;
      right: 0;
      bottom: 0;
      left: 0;

      & > * {
        object-fit: cover;
        object-position: top;
        height: 100%;
        width: 100%;
      }
    }

    @include for-size(phone-only) {
      top: auto;
      bottom: 0;
    }
  }

  &__prices {
    display: flex;
    gap: 30px;
    max-width: 1170px;
    margin: 0 auto 137px;

    @include for-size(tablet) {
      flex-direction: row;
      flex-wrap: wrap-reverse;
      justify-content: center;
      gap: 30px 50px;
      margin-bottom: 124px;
    }

    @include for-size(phone-only) {
      flex-direction: column;
      flex-wrap: wrap;
      align-items: center;
      gap: 30px;
      margin-bottom: 52px;
    }
  }
}
